#button {
  font-family: 'Lobster', cursive;
  background: #222;
  height: 50px;
  min-width: 150px;
  border: none;
  border-radius: 10px;
  color: #eee;
  font-size: 40px;
  font-family: 'Cookie', cursive;
  position: relative;
  transition: 1s;
  -webkit-tap-highlight-color: transparent;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  padding-top: 5px;
}

.noselect {
  font-family: 'Lobster', cursive;
  -webkit-touch-callout: none;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

#button #circle {
  width: 5px;
  height: 5px;
  background: transparent;
  border-radius: 50%;
  position: absolute;
  top: 0;
  left: 50%;
  overflow: hidden;
  transition: 500ms;
}
#button:hover {
  background: transparent;
}

#button:hover #circle {
  height: 50px;
  width: 150px;
  left: 0;
  border-radius: 0;
  border-bottom: 2px solid #eee;
}

.creer-btn {
  margin-left: 10px;
  margin-top: 10px;
  width: 15%;
  font-family: Hack, monospace;
  background: #3decec;
  color: #1d1d1d;
  cursor: pointer;
  font-size: 25px;
  padding: 1.0rem;
  border: 0;
  transition: all 0.5s;
  border-radius: 10px;
  width: auto;
  position: relative;
}
.creer-btn::after {
  background-image: url(/Soccer/static/media/f2f6.4e2110a5.png);
  font-family: "Font Awesome 5 Pro";
  font-weight: 400;
  position: absolute;
  left: 80%;
  top: 54%;
  right: 0;
  bottom: 0;
  opacity: 0;
  transform: translate(-50%, -50%);
  background: black;
}
  .creer-btn:hover {
      background: #e2463a;
      transition: all 0.5s;
      border-radius: 10px;
      box-shadow: 0px 6px 15px #f50d0561;
      padding: 1.5rem 3rem 1.5rem 1.5rem;
      color: #ffffff;
  }
  .creer-btn::after {
    opacity: 1;
    transition: all 0.5s;
    color: #ffffff;
}

